<template>
  <div class="editart">
    <ul class="list">
      <li>
        <h2>文章列表</h2>
        <h2>总计{{ blogList.length }}篇</h2>
      </li>
      <li v-for="(v, index) in blogList" :key="index">
        <h2>{{ v.title }}</h2>
        <div>
          <p @click="edit(v.id)">编辑</p>
          <p @click="delet(v.id)">删除</p>
          <p @click="manageMeg(v.id)">管理留言</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import { log } from 'three';
import { Blog, delArt } from '../../api/index'
export default {
  inject: ['reload'],
  name: "EditArticle",
  created() {
    Blog(1000)
      .then((res) => {
        if (res) {
          this.blogList = res.data;
          console.log('EditArticle中获取全部文章列表', res);
        }
      })
      .catch(function (error) {
        console.log(error);
      });
  },
  data() {
    return {
      // 文章列表
      blogList: [],

    }
  },
  methods: {
    // 编辑
    edit(id) {
      console.log('请求addart,对应组件为createArt,在里面进行修改操作,需要传该文章的id');
      this.$router.push({ path: "/addart", query: { id: id } });
    },
    // 删除
    delet(id) {
      delArt(id)
        .then((res) => {
          if (res) {
            console.log('EditArticle中删除id的博客的结果', res);
            if (res.code == '200') {
              this.$message({
                message: '删除文章成功',
                type: 'success',
                duration: 1000
              });
              // this.reload();
              // 删除文章之后需要刷新
              this.reFresh()
            } else {
              this.$message({
                showClose: true,
                message: '删除文章失败',
                type: 'error',
                duration: 1000
              });
            }
          }
        })
        .catch(function (error) {
          console.log(error);
        });


    },
    // 留言管理
    manageMeg(id) {
      console.log('请求editmeg,对应组件为EditMeg,在里面进留言管理操作,需要传该文章的id');
      this.$router.push({ path: "/editmeg", query: { id: id } });
    },
    reFresh() {
      Blog(1000)
        .then((res) => {
          if (res) {
            this.blogList = res.data;
            console.log('EditArticle中删除之后刷新全部文章列表', res);
          }
        })
        .catch(function (error) {
          console.log(error);
        });
    }
  },

};
</script>
